import { Component, ViewEncapsulation,ElementRef, ViewChild } from '@angular/core';
import { AIService } from './../services/AIService';
import { BotTrainingService } from './../services/BotTrainingService';
import { BlockGroupsModel } from './../models/BlockGroupsModel';

declare var jQuery: any;
declare var Messenger: any;
//select2GroupedData: Select2OptionData[] = [];
@Component({
  selector: '[buildai]',
  template: require('./buildai.component.html'),
  providers:[AIService, BotTrainingService],
  encapsulation: ViewEncapsulation.None,
  styles: [require('./buildai.component.scss')]
})
export class BuildAI {
  public aiService : AIService;
  public botTrainingService : BotTrainingService;
  public phraseGroups = [];
  public phraseText:string;
  public blockGroupsModel : BlockGroupsModel [] = [];
  public select2GroupedData = []
    constructor(public _aiService : AIService,public _botTrainingService: BotTrainingService)
    {
      this.phraseGroups = [];
      this.populate();
      this.populateBlocks();
    }
    populateBlocks()
    {
      this._botTrainingService.getAllGroups().subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                          this.blockGroupsModel = a.data;
                            for(var i = 0; i<this.blockGroupsModel.length;i++)
                                {
                                    if(this.blockGroupsModel[i].blocks.length > 0)
                                    {
                                        for(var j = 0;j<this.blockGroupsModel[i].blocks.length; j++)
                                        {
                                            this.select2GroupedData.push({id:this.blockGroupsModel[i].blocks[j]._id, text:this.blockGroupsModel[i].blocks[j].name});
                                        }
                                        
                                    }
                                }
                            console.log(this.select2GroupedData);
                        }           
                    }
                );
    }
    changeDropdown(blockId, phaseGroupId)
    {
      for(var i=0; i< this.select2GroupedData.length;i++)
      {
        if(this.select2GroupedData[i].id == blockId)
        {
          jQuery('#block'+phaseGroupId).html(this.select2GroupedData[i].text + " <span class='caret'></span></button>");
          this._aiService.updatePhraseGroup(blockId,phaseGroupId).subscribe(
             a=> {
                  if(a.code == 200)
                     {
                     }           
                 }
            );
        }
      }
    }
    populate()
    {
       this._aiService.getAllphrases().subscribe(
                    a=> {
                        if(a.code == 200)
                        {
                            //console.log(a.data[1].phraseGroup._blockId);
                            this.phraseGroups = a.data;
                            console.log(this.phraseGroups);
                        }           
                    }
                );
    }
    addAiRule(){

        this._aiService.postPhrasesGroups().subscribe(
             a=> {
                  if(a.code == 200)
                     {
                       this.populate();
                     }           
                 }
            );
    }
    onKey(event, _phraseGroupId)
    {
      console.log(_phraseGroupId);
      if(event.keyCode == 13) {
        this._aiService.addPhrase(jQuery('#phraseText'+_phraseGroupId).val(),_phraseGroupId).subscribe(
             a=> {
                  if(a.code == 200)
                     {
                       for(var i = 0; i< this.phraseGroups.length; i++)
                       {
                         if(this.phraseGroups[i].phraseGroup._id == _phraseGroupId)
                         {
                           this.phraseGroups[i].phrases.push(a.data);
                         }
                       }
                     }           
                 }
            );
      }
    }
    over(id)
    {
      jQuery('.close').hide();
      var val = '#deleteGroup'+id;
      console.log(val);
      jQuery(val).show();
    }
    leave(id)
    {
      var val = '#deleteGroup'+id;
      console.log(val);
      jQuery(val).hide();
    }
}




