import {Injectable,Inject} from '@angular/core';
import {Http,Headers, RequestOptions} from '@angular/http';
import 'rxjs/add/operator/map';
@Injectable()
export class AIService{
    baseUrl:string = "http://aprilappserver.azurewebsites.net/";
    //baseUrl:string = "http://localhost/";
    constructor(private http:Http)
    {

    }
    getAllphrases()
    {
        return this.http.get(this.baseUrl + 'phrases/getAllphrases')
            .map(res=>res.json());
    }
    addPhrase(phraseText, _phraseGroupId)
    {
        let body = JSON.stringify({ "phraseText":phraseText, '_phraseGroupId': _phraseGroupId });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method:'post', headers: headers });

        return this.http.post(this.baseUrl + "phrases/addPhrase", body, options)
            .map(res=>res.json());
    }
    updatePhraseGroup(_blockId, _phraseGroupId)
    {
        let body = JSON.stringify({ "_blockId":_blockId, '_phraseGroupId': _phraseGroupId });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method:'post', headers: headers });

        return this.http.post(this.baseUrl + "phrasegroup/updatePhraseGroup", body, options)
            .map(res=>res.json());
    }
    deleteUsers(Id)
    {
        return this.http.get(this.baseUrl + 'users/deleteUser/'+Id)
            .map(res=>res.json());
    }
    postPhrasesGroups()
    {
        let body = JSON.stringify({'id':0 });
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method:'post', headers: headers });

        return this.http.post(this.baseUrl + "phrasegroup/addPhraseGroup", body, options)
            .map(res=>res.json());
    }
}


